import React from 'react'

import {
  ContactForm,
  JobHistory,
  ProjectShowcase,
  CallToAction,
  Navbar,
  Container,
  Seo
} from '../components'
import Footer from '../components/Footer'
import FloatingScrollButton from '../components/FloatingScrollButton'
import { graphql } from 'gatsby'

const navbarItems = [
  { label: 'Home', link: '#home' },
  { label: 'Portfolio', link: '#portfolio' },
  { label: 'Working experience', link: '#job-history' },
  { label: 'Contact me', link: '#contact-me' }
]
function Home () {
  return (
    <>
      <Seo />
      <Container bgColor='dark'>
        <Navbar isSticky={false} navbarItems={navbarItems} />
        <CallToAction />
      </Container>
      <Container bgColor='light'>
        <ProjectShowcase />
      </Container>
      <Container bgColor='dark'>
        <JobHistory />
      </Container>
      <Container bgColor='light'>
        <ContactForm />
      </Container>
      <Container bgColor='dark'>
        <Footer navbarItems={navbarItems} />
      </Container>
      <FloatingScrollButton />
    </>
  )
}
export const pageQuery = graphql`
  query {
    file(relativePath: { eq: "cat_1.jpg" }) {
      id
      childImageSharp {
        fluid {
          sizes
          src
          srcSet
        }
        gatsbyImageData
      }
    }
  }
`
export default Home
